import React from "react";
import "../hojas-de-estilo/Tarea.css"
import { AiOutlineCloseCircle } from "react-icons/ai";

/*
2.
  completarTarea: es una funcin que se va a ejecutar cuando el usuario de clic a "tarea-texto".
  En el elemento "tarea-texto" se est poniendo un onClick y dentro una funcin annima la cual llamar a la funcin "completarTarea" con el parmetro del "id" de la tarea para que sepa que tarea se mostrar como completada de toda la lista de tareas del array "tareas" del componente "ListaDeTareas". 

  eliminarTarea: es una funcin que se ejecutar cuando se de clic al icono. En "tarea-contenedor-iconos". Tiene la misma lgica de completarTarea. Tiene como parmetro el id para que sepa que objeto del array "tareas" eliminar. 
*/

function Tarea({id, texto, completada, completarTarea, eliminarTarea}){
  return(
    <div className={completada ? "tarea-contenedor completada" : "tarea-contenedor"}>
      <div 
        className="tarea-texto"
        onClick={() => completarTarea(id)}>
        {texto}
      </div>
      <div 
        className="tarea-contenedor-iconos"
        onClick={()=>eliminarTarea(id)}>
        <AiOutlineCloseCircle className="tarea-icono"/>
      </div>
    </div>
  )
}

export default Tarea;