import React, {useState} from "react";
import "../hojas-de-estilo/ListaDeTareas.css"
import TareaFormulario from "./TareaFormulario";
import Tarea from "./Tarea";

/*
1. 
  Mostrar una lista de componentes de react:
  Se va a renderizar varios componentes Tarea.jsx. Cada una de ellas tendr sus propios valores prop. 
  Se abren llaves {} porque se va a necesitar un arreglo de js. 
  Se toma el arreglo "tareas" que es el primer valor de useState el cual cambiar de valor y tiene como valor inicial un arreglo vacio. Lo que hay dentro de "tareas" es el objeto que est en el componente "TareaFormulario". 
  Cada vez que se ejecute la funcin de "submit" se renderzar y actualizar el valor de "tareas" aumentando o restando objetos.
  Luego de llamar al arreglo se llama a la funcin "map". Para tener tareas.map().
  map: es un mtodo que tomar cada uno de los objetos que tenga el array "tareas" y los actualizar agregando o restando elementos. 
  Para cada objeto el callback "tarea" representa el objeto actual que se est procesando.  
  Luego se crea un componente "Tarea" para cada uno de esos objetos que se est pasando en el callback. Esos datos de el objeto actual se usar para crear un componente y llenar los props. En este caso se est pasando el id, texto y completada. Los dems props se rellenarn con otros datos. Cada vez que se haga submit en el formulario y se actualize el valor de "tareas" con la funcin "agregarTarea" se aade una componente ms de "Tarea.jsx".

4.
  TareaFormulario: se aadir el prop "onSubmit" y se le dar el valor de la funcin "agregarTarea". Recordar que la funcin onSubmit se ejecuta cuando el formulario se enva. Es decir, que al enviar el formulario se llamar a la funcin "agregarTarea". 

  Comunicacin entre los componentes: se est pasando la funcin "agregarTarea" al componente "TareaFormulario" a travs de un hook. Luego el "TareaFormulario" a travs de los props est dandole el valor al parmetro "tarea" con "tareaNueva" (objeto creado en TareaFormulario). Es decir ahora "tarea" es "tareaNueva". 

  agregarTarea: ponemos un condicional para saber si la tarea no es null. trim() es una mtodo para saber los espacios que hay en una cadena de texto. Si es 0 es porque es porque no hay ningun caracter. Al no haber ningn caracter retorna false. Si es ms de 1 es true ya que si retorna caracteres. Este mtodo es como los que deben de retornar falsy o truly acorde si est vaco o no.De esta manera se confirmar que la cadena no est vaca. Luego para quitarle los espacios usamos el mismo trim(). Para colocar una tarea nueva al principio se crea la variable "tareasActualizadas" y se incluye dentro al inicio "tarea" . Luego tomamos todas las tareas anteriores y con el operador spread(...) se aaden las dems tareas incluidos en "tareas".

  Operador spread: El operador spread en JavaScript, representado por tres puntos (...), es una sintaxis que permite expandir o descomponer elementos de un iterable (como un array o un objeto) en lugares donde se esperan mltiples elementos o propiedades. Es muy til para copiar y combinar arrays u objetos de manera ms fcil y legible. Ejemplos:
  const arr1 = [1, 2, 3];
  const arr2 = [...arr1]; // arr2 ser [1, 2, 3]

  const arr1 = [1, 2, 3];
  const arr2 = [4, 5, 6];
  const arr3 = [...arr1, ...arr2]; // arr3 ser [1, 2, 3, 4, 5, 6]

  Cuando se muestra una lista en react como en "tareas-lista-contenedor" para que react sepa el orden y no los cambie al actualizarlos. Se debe pasar un atributo llamado "key". 

  eliminarTarea: se crea una constante igual que en la funcin de agregarTarea. Esta funcin filtra una tarea especifica de todas las "tareas". Esto se logra con el mtodo .filter. El que cumpla la condicin que est dentro de los parntesis es la tarea que se incluir en "tareas". 
  
  tareas.filter(): aqu se est utilizando el mtodo .filter() en el array "tareas" para crear un nuevo array basado en una condicin.
  tarea => ...: esta parte utiliza una funcin de flecha para definir la funcin de callback y "tarea" es el nombre que se le da a cada elemento del array mientras se itera sobre l. En este contexto, tarea representa un objeto de tarea individual en el array.
  tarea.id !== id: aqu se est evaluando cada tarea. La condicin que se est verificando es si todos los tarea.id de todas las tareas es diferente (!==) del id (el tarea.id de la tarea seleccionada) entonces no se incluye en "tareas";
*/
function ListaDeTareas (){
  const[tareas, setTareas]=useState([]);
  const agregarTarea = tarea => {
    if(tarea.texto.trim()){
      tarea.text = tarea.texto.trim();
      const tareasActualizadas = [tarea, ...tareas];
      setTareas(tareasActualizadas);    
    } 
  }

  const eliminarTarea = id =>{    
    const tareasActualizadas = tareas.filter(tarea => tarea.id !== id);    
    setTareas(tareasActualizadas);        
  }

  const completarTarea = id =>{
    const tareasActualizadas = tareas.map(tarea => {
      if(tarea.id === id){
        tarea.completada = !tarea.completada;
      }
      return tarea;
    })
    setTareas(tareasActualizadas);
  }
  return(
    <>
      <TareaFormulario onSubmit={agregarTarea}/>
      <div className="tareas-lista-contenedor">
        {
          tareas.map((tarea)=>
            <Tarea
              key={tarea.id}
              id={tarea.id}
              texto={tarea.texto}
              completada={tarea.completada}
              completarTarea={completarTarea}
              eliminarTarea={eliminarTarea}
            />
          )
        }        
      </div>
    </>
  )
}
export default ListaDeTareas;