import React, {useState} from "react";
import "../hojas-de-estilo/TareaFormulario.css";
import {v4 as uuidv4} from "uuid";

  /*
  3.
  manejarEnvio: funcin para poder agregar una tarea al dar clic en el elemento "tarea-boton". Tiene como argumento un evento que se mandar automticamente cuando ocurra dicho envo.  
  
  manejarCambio: debemos manejar que ocurre cuando cambia el contenido del formulario. Es decir, cuando el usuario est escribiendo debemos indicar que es lo que va a ocurrir. Para esto se trabajar una vez ms con "hooks". Se tendr como valor inicial del primer elemento "input" como vaco. Cuando el usuario escriba en dicho campo de texto se actualizar el valor de "input" a lo que escribi el usuario. 

  Debemos manejar el cambio que habr cuando el usuario escriba en el cuadro de texto y luego poder crear "tareaNueva" con dicho texto que ingres el usuario. En el elemento "input" de "tarea-input" se agregar un eventListener llamado "onChange". Dicho evento en input se ejecuta cada vez que el usuario escriba en el cuadro de texto. 

  manejarCambio: luego de crear dicho evento se usar la funcin "setInput" del hook. Se usar e.target.value. Que quiere decir el valor del elemento actual donde se est ejecutando el evento. En este caso lo que escribi el usuario en la caja de texto que es donde se est ejecutando dicho evento. 

  manejarEnvio: luego de tener el valor de lo que escribi el usuario en el "input" ahora debemo enviar dicho valor con la funcin manejarEnvio. Dicho evento se ejecutar con el eventListener "onSubmit" del elemento form. Necesitamos que al hacer un "submit" al formulario no se actualice la pgina. Sino que solo se agregue dichos elementos mientras se hace clic en el botn. Para esto se usa el mtodo e.preventDefault() dentro de la funcin "manejarEnvo".

  Luego de ello en el objeto "tareaNueva" el valor "texto" se pone el "input" (el cual es el texto de la caja). Tambin se le est dando otro valor al objeto llamado "completada" el cual es "false" como valor inicial.

  Generar id: se genera el id de las tareas con un paquete nuevo. El paquete se llama "uuid". Dicho paquete se usa para crear identificadores nicos. Para instalarlo se escribe en el terminal "npm install uuid". Luego de instalarlo se importa de esta manera: import {v4 as uuidv4} from "uuid". Luego de ser instalado e importado se coloca en el valor de id uuidv4(). 

  Ahora el componente que tenemos se va a pasar al componente "Lista de Tareas". Para poder actualizar dicha lista de tareas que es un array. Se dar un prop. En este caso ser ".onSubmit(tareaNueva)". Y el valor lo pasar "ListaDeTareas". (ir ah)
  */

function TareaFormulario(props){

  const [input, setInput] = useState('');

  const manejarCambio = e => {
    setInput(e.target.value);    
  }
  
  const manejarEnvio = e => {
    e.preventDefault();
    const tareaNueva ={
      id: uuidv4(),
      texto: input,
      completada: false
    }
    props.onSubmit(tareaNueva);
  }

  return(
    <form 
      className="tarea-formulario"
      onSubmit={manejarEnvio}>
      <input 
        className="tarea-input"
        type="text"
        placeholder="Escribe una tarea"
        name="texto"
        onChange={manejarCambio}
      />
      <button className="tarea-boton">Agregar Tarea</button>
    </form>
  )
}

export default TareaFormulario